.btn {
  display: inline-block;
}

.btn--grad {
  background-color: #e32c75;
  background-image: linear-gradient(135deg, #ff974f 0%, #e32c75 100%);
}

.user-info {
  position: relative;
}

.user-info__pic {
  width: 32px;
  height: 32px;
  background-color: #ecf1f8;
  overflow: hidden;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.user-info__avatar {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

.drpd-menu {
  display: none;
}

.drpd-menu.open {
  display: block;
}

.switcher__btn::before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.switcher__inp:checked ~ .switcher__btn::before {
  right: 0;
  left: auto;
}

.stat-list__itm {
  line-height: 1;
}

@media (min-width: 320px) {
  .layout--has-fixed-head {
    padding-top: 52px;
  }

  .layout--has-fixed-btn {
    padding-bottom: 40px;
  }

  .supwrap {
    max-width: 360px;
  }

  .main-head {
    background-color: white;
    box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.06);
  }

  .main-head--fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
  }

  .main-head__top {
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .main-head__nav {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.96);
    padding: 20px 0;
    display: none;
    box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.06);
    border-top: 1px solid #ecf1f8;
  }

  .main-head__nav.open {
    display: block;
  }

  .main-head__addwr {
    width: 100%;
    position: relative;
  }

  .main-head__add {
    width: 100%;
    height: 40px;
    position: fixed;
    bottom: 0;
    left: 0;
  }

  .main-head__add::before,
  .main-head__add::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background-color: white;
  }

  .main-head__add::before {
    width: 20px;
    height: 2px;
  }

  .main-head__add::after {
    width: 2px;
    height: 20px;
  }

  .status-bar {
    display: flex;
  }

  .status-bar__icons {
    order: 1;
  }

  .status-bar__icn::before {
    width: 32px;
    height: 32px;
    background-size: 32px;
  }

  .status-bar__mnu {
    order: 2;
  }

  .user-info__name {
    display: none;
  }

  .user-info__logged {
    padding-right: 20px;
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
  }

  .user-info__logged::before {
    width: 16px;
    height: 16px;
    background-size: 16px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }

  .icns-lst {
    display: flex;
    align-items: center;
  }

  .icns-lst__itm--hm {
    display: none;
  }

  .icns-lst__lnk {
    display: flex;
    align-items: center;
    text-decoration: none;
  }

  .icns-lst__val {
    line-height: 1;
  }

  .menu__link {
    position: relative;
    display: block;
    padding: 5px 10px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
    color: rgba(62, 63, 88, 0.6);
    text-decoration: none;
    transition: all .5s;
  }

  .menu__link::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 4px;
    height: 100%;
    background-color: transparent;
    transition: all .5s;
  }

  .menu__link--active {
    color: #3e3f58;
  }

  .menu__link--active::before {
    background-color: #ed5267;
  }

  .hbg {
    width: 22px;
    height: 16px;
    padding: 0;
    position: relative;
  }

  .hbg__line {
    width: 100%;
    border-top: 1px solid;
    border-bottom: 1px solid;
    position: absolute;
    left: 0;
    transform-origin: 50%;
    transition: transform .45s;
  }

  .hbg__line--top {
    top: 0;
  }

  .hbg__line--mid {
    top: calc(50% - 1px);
  }

  .hbg__line--bot {
    top: calc(100% - 2px);
  }

  .hbg.open .hbg__line--top {
    transform: rotate(45deg);
    top: calc(50% - 1px);
  }

  .hbg.open .hbg__line--mid {
    display: none;
  }

  .hbg.open .hbg__line--bot {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }

  .drpd__toggler::before {
    width: 16px;
    height: 16px;
    background-size: 16px;
  }

  .drpd-menu__lst {
    background-color: white;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 8px;
    box-shadow: 0px 24px 24px rgba(0, 0, 0, 0.12);
    position: absolute;
    top: calc(100% + 16px);
    z-index: 99;
    min-width: 176px;
  }

  .drpd-menu__lnk {
    display: flex;
    align-items: center;
    padding: 9px 24px;
    text-decoration: none;
    transition: all .45s;
    font-weight: 600;
  }

  .drpd-menu__icn::before {
    width: 20px;
    height: 20px;
    background-size: 20px;
  }

  .drpd-menu__icn {
    margin-right: 3px;
  }

  .content__parts {
    padding-top: 30px;
  }

  .content__part {
    margin-bottom: 32px;
  }

  .content__swtch {
    padding: 0 10px 20px;
  }

  .card-list__itm {
    margin-bottom: 24px;
  }

  .card-list__card {
    height: 100%;
  }

  .switcher__inner {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
  }

  .switcher__lbl {
    margin: 0 20px;
  }

  .switcher__btn {
    display: inline-block;
    width: 40px;
    height: 24px;
    position: relative;
    background-color: #ed5267;
    border: 3px solid #ed5267;
    border-radius: 18px;
    cursor: pointer;
  }

  .profile__head {
    position: relative;
    margin-bottom: 20px;
    padding-right: 30px;
  }

  .profile__details {
    display: flex;
    margin-bottom: 32px;
  }

  .profile__pic {
    overflow: hidden;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: 24px;
  }

  .profile__avatar {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .profile__name {
    font-family: "Montserrat", sans-serif;
    font-size: 24px;
    font-weight: 600;
    line-height: 1.08;
    margin-bottom: 8px;
  }

  .profile__uname {
    margin-bottom: 16px;
  }

  .profile__edit {
    display: flex;
    align-items: center;
  }

  .profile__edit {
    color: #ed5267;
  }

  .profile__edit:visited {
    color: #ed5267;
  }

  .profile__edit:hover {
    color: #ed5267;
  }

  .profile__edit:active,
  .profile__edit:focus {
    color: #ed5267;
  }

  .profile__edicn {
    margin-right: 8px;
  }

  .profile__edicn::before {
    width: 20px;
    height: 20px;
    background-size: 20px;
  }

  .profile__stat-list {
    display: flex;
    justify-content: space-between;
  }

  .profile__drpd {
    position: absolute;
    right: 0;
  }

  .profile__dml {
    right: 0;
  }

  .stat-list__itm--ctr {
    text-align: center;
  }

  .stat-list__count {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  .stat-list__desc {
    font-size: 16px;
    font-weight: 400;
  }

  .card__cont {
    height: 100%;
    min-height: 280px;
    background-color: white;
    box-shadow: 0px 24px 24px rgba(0, 0, 0, 0.12);
    display: flex;
    flex-direction: column;
    padding: 24px;
    border-radius: 8px;
  }

  .card__shrd {
    min-height: 22px;
    line-height: 22px;
    display: flex;
    align-items: center;
  }

  .card__ttl {
    font-family: "Montserrat", sans-serif;
    font-size: 26px;
    font-weight: 400;
    line-height: 1.2;
  }

  .card__lnk {
    text-decoration: none;
  }

  .card__lnk {
    color: #3e4056;
  }

  .card__lnk:visited {
    color: #3e4056;
  }

  .card__lnk:hover {
    color: #3e4056;
  }

  .card__lnk:active,
  .card__lnk:focus {
    color: #3e4056;
  }

  .card__meta {
    margin-top: auto;
    padding-top: 7px;
  }

  .card-shinf__type {
    white-space: nowrap;
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
  }

  .card-shinf__lbl {
    display: inline-block;
    border-color: #ecf1f8;
    border-style: solid;
    border-width: 3px 5px 5px 4px;
    border-radius: 6px;
    overflow: hidden;
    margin-left: 8px;
    background-color: #ecf1f8;
    line-height: 1;
  }

  .card-shinf__icn {
    margin-right: 2px;
  }

  .card-shinf__icn::before {
    width: 16px;
    height: 16px;
    background-size: 16px;
  }

  .card-meta {
    display: flex;
    justify-content: space-between;
  }

  .card-meta__aut {
    display: flex;
    align-items: center;
    text-decoration: none;
  }

  .card-meta__pic {
    width: 24px;
    height: 24px;
    background-color: #b2b3bb;
    overflow: hidden;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
  }

  .card-meta__usa {
    width: 100%;
    height: 100%;
    object-fit: contain;
    display: block;
  }

  .card-meta__nm {
    font-size: 14px;
    font-weight: 600;
  }

  .card-meta__icons {
    font-size: 14px;
    font-weight: 600;
    color: #3e4056;
  }

  .card-meta__icn {
    margin-left: 16px;
    margin-right: 8px;
  }

  .card-meta__icn::before {
    width: 16px;
    height: 16px;
    background-size: 16px;
  }

  .card-meta__shbt {
    text-decoration: none;
    font-size: 18px;
  }

  .card-meta__shbt {
    color: #ed5267;
  }

  .card-meta__shbt:visited {
    color: #ed5267;
  }

  .card-meta__shbt:hover {
    color: #ed5267;
  }

  .card-meta__shbt:active,
  .card-meta__shbt:focus {
    color: #ed5267;
  }
}

@media (min-width: 768px) {
  .supwrap {
    max-width: 750px;
  }

  .icns-lst__itm--hm {
    display: block;
  }

  .card-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .card-list__itm {
    width: calc(50% - 12px);
  }
}

@media (min-width: 992px) {
  .supwrap {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .layout--has-fixed-head {
    padding-top: 112px;
  }

  .layout--has-fixed-btn {
    padding-bottom: 0;
  }

  .supwrap {
    max-width: 1200px;
  }

  .main-head__top {
    padding-top: 21px;
  }

  .main-head__hbg {
    display: none;
  }

  .main-head__nav {
    position: relative;
    top: auto;
    left: auto;
    display: block;
    box-shadow: none;
    border-top: none;
    padding: 0px;
  }

  .main-head__add {
    width: 64px;
    height: 64px;
    position: absolute;
    top: 0%;
    right: 0;
    left: auto;
    transform: translateX(-15px) translateY(-50%);
    border-radius: 50%;
  }

  .status-bar__icons {
    order: 2;
  }

  .status-bar__mnu {
    order: 1;
    margin-right: 20px;
  }

  .user-info__name {
    display: block;
    margin-right: 20px;
  }

  .user-info__logged {
    padding-right: 30px;
  }

  .menu {
    display: flex;
  }

  .menu__itm:not(:first-child) {
    margin-left: 37px;
  }

  .menu__link {
    padding: 5px 0px 20px;
  }

  .menu__link::before {
    top: auto;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 4px;
  }

  .menu__link--active::before {
    width: 100%;
    height: 4px;
  }

  .menu__link:hover {
    color: #3e3f58;
  }

  .menu__link:hover::before {
    width: 100%;
    height: 4px;
    background-color: #ed5267;
  }

  .drpd-menu__itm:hover {
    background-color: #ecf1f8;
  }

  .content__parts {
    display: flex;
    padding-top: 72px;
  }

  .content__part--l {
    flex: 0 0 374px;
    padding-right: 24px;
  }

  .content__part--r {
    flex: 0 0 796px;
    padding-left: 24px;
  }

  .content__swtch {
    padding: 0 10px 60px;
  }

  .profile__dml {
    right: auto;
    left: 0;
  }

  .card__ttl {
    font-size: 36px;
  }
}